import Data from "@/js/core/data";

const Mocks_Server = "https://api.${region}.500apps.com/mockly";

class MockFrame {
  getProject(domain_id, project_id, project_name) {
    let data = new Data(`${Mocks_Server}/project/${domain_id}/${project_id}`);
    data.params = `p=${project_name}`;
    data.disablePCors();
    return data.get();
  }

  getMocks(domain_id, project_id, project_name, mock_id, mock_name) {
    let data = new Data(`${Mocks_Server}/${domain_id}/${project_id}`);
    data.params = mock_id && mock_name ? `p=${project_name}&mid=${mock_id}&m=${mock_name}&order_by=created_date desc` : `p=${project_name}&order_by=created_date desc`;
    data.disablePCors();
    return data.get();
  }
  editMocks(domain_id, project_id, project_name, mock_id, mock_name, notes_json) {
    let params = `p=${project_name}&m=${mock_name}`;
    let data = new Data(`${Mocks_Server}/update/${domain_id}/${project_id}/${mock_id}?${params}`);
    data.disablePCors();
    return data.edit(mock_id, { notes_json: notes_json });
  }
}

export default new MockFrame();
