<template>
  <div>
    <template v-if="!isLoading">
      <b-row v-if="mocks && mocks.length" class="m-0">
        <b-col class="col-2">
          <h4>Mocks</h4>
          <b-row class="px-2 " v-for="(mock, index) in mocks" :key="index">
            <b-link class="btn btn-outline-light height-75 btn-block text-center text-dark show-event-radio pt-1 pb-2" :class="index == activeIndex ? 'active' : ''" @click="getMockData(mock, index)">
              <b-row class="justify-content-between px-3" v-if="mock.name">
                <span class="event-title mr-4 pr-6 pt-2 mt-1 text-truncate ">
                  {{ mock.name }}
                </span>
              </b-row>
            </b-link>
          </b-row>
        </b-col>
        <b-col class="col-8 overflow-auto" v-if="mocks && mocks.length">
          <div>
            <div v-for="(comment, index) in notes_json" :key="index">
              <div class="d-flex" :style="{ position: `absolute`, top: `${comment.top}px`, left: `${comment.left}px` }" @click="showEditCommentBox(index + 1)">
                <span class="mr-1 align-middle fe fe-message-circle" :style="`color: ${commentActiveIndex == index ? '#FF0000' : '#000000'}`"></span>
                <Avatar :username="comment && comment.username ? comment.username : 'Anonymous'" :size="25" />
              </div>
            </div>
          </div>
          <div v-if="commentIndex && !show_comment" :style="{ position: `absolute`, top: `${top}px`, left: `${left}px` }" @click="showEditCommentBox()">
            <span class="mr-1 align-middle fe fe-message-circle" style="color: #FF0000"></span>
          </div>
          <div v-if="show_comment && !commentIndex">
            <b-form-group :label="label" :style="{ position: `absolute`, top: `${top + 20}px`, left: `${left}px` }">
              <b-form-input v-if="!commentIndex" placeholder="Your name" v-model="username" class="form-control form-control-sm small" />
              <b-form-textarea class="mt-1" type="text" rows="1" v-model="comment" placeholder="Please enter comment" />
              <button :disabled="!username" class="btn btn-primary btn-sm m-1 mt-2" @click="saveComment()">{{ btn_comment }}</button>
              <button class="btn btn-secondary btn-sm m-1 mt-2" @click="hideCommentBox()">Cancel</button>
            </b-form-group>
          </div>
          <img v-if="mocks && mocks[activeIndex] && mocks[activeIndex].url" ref="mock_img" :src="mocks[activeIndex].url" alt="Mock" @click="getCoordinates($event)" />
          <b-overlay :show="isBusy && !isLoading" no-wrap opacity="0.9" spinner-variant="primary"></b-overlay>
        </b-col>
        <b-col class="col-2">
          <h3>Comments</h3>
          <hr />
          <div
            class=" btn btn-outline-light height-75 btn-block text-dark show-event-radio pt-1 pb-2"
            :class="{ active: index == commentActiveIndex }"
            v-for="(comment, index) in notes_json"
            :key="index"
          >
            <b-link class="height-75 btn-block text-dark show-event-radio pt-1 pb-2" @click="(show_comment = false), getComment(comment, index)">
              <template v-if="comment && comment.comment">
                <div class="d-flex align-items-center">
                  <Avatar :username="comment && comment.username ? comment.username : 'Anonymous'" :size="25" />
                  <h4 class="pl-2 mb-0">
                    {{ comment && comment.username ? comment.username : "Anonymous" }}
                  </h4>
                  <span class="text-muted ml-auto" v-b-tooltip.hover :title="comment.commented_on">{{ moment(comment.commented_on).fromNow() }}</span>
                </div>
                <div class="text-left mt-2 ml-4 pl-2" style="word-break: break-word;">
                  {{ comment.comment }}
                </div>
              </template>
            </b-link>
            <div class="w-100 text-right">
              <span class="fe fe-edit mr-2" v-b-tooltip:hover title="Edit Comment" @click="(show_comment = true), getComment(comment, index)" />
              <span class="fe fe-trash-2 option-icon-delete" v-b-tooltip:hover title="Delete Comment" @click="deleteComment(index)" />
            </div>
            <div v-if="show_comment && commentIndex && commentIndex == index + 1" class="text-left" :class="{ active: 0 }">
              <b-form-group :label="label">
                <b-form-textarea class="mt-1" type="text" rows="1" v-model="edit_comment" placeholder="Please enter comment" />
                <button :disabled="!username" class="btn btn-primary btn-sm m-1 mt-2" @click="saveComment()">{{ btn_comment }}</button>
                <button class="btn btn-secondary btn-sm m-1 mt-2" @click="hideCommentBox()">Cancel</button>
              </b-form-group>
            </div>
          </div>
        </b-col>
      </b-row>
      <b-row v-else class="align-items-center px-1 row">
        <div class="mx-auto py-5 text-center">
          <img src="/img/mockframe/no_mock_found.svg" width="300" class="my-4" />
          <h1 class="mb-2 text-black-50">No mocks Found</h1>
        </div>
      </b-row>
    </template>
    <b-overlay :show="isLoading" no-wrap opacity="0.9" spinner-variant="primary"></b-overlay>
  </div>
</template>
<script>
  import MockFrame from "@/js/mockframe/mockframe";
  import moment from "moment";
  import Avatar from "vue-avatar";

  export default {
    props: ["project"],
    components: { Avatar },
    data() {
      return {
        domainId: window.domain,
        projectId: window.user,
        mockId: window.id,
        mocks: [],
        notes_json: [],
        isBusy: false,
        activeIndex: null,
        projectName: "",
        mockName: "",
        show_comment: false,
        comment: "",
        commentActiveIndex: 0,
        label: "Add comment",
        btn_comment: "Add",
        top: 0,
        left: 0,
        commentIndex: 0,
        isLoading: true,
        username: "",
        edit_comment: "",
      };
    },
    async mounted() {
      await this.getMocks();
      if (this.mocks && this.mocks.length) this.getMockData(this.mocks[0], 0);
    },
    methods: {
      // Get co-ordinates of mouse click on mock and display the comment box
      getCoordinates(event) {
        this.top = event.clientY - this.$refs.mock_img.getBoundingClientRect().top;
        this.left = event.clientX - this.$refs.mock_img.getBoundingClientRect().left;
        this.showCommentBox();
      },
      // Get mocks data
      async getMocks() {
        try {
          this.isLoading = true;
          this.projectName = this.$route.query.p;
          this.mockName = this.$route.query.m;
          this.mocks = await MockFrame.getMocks(this.domainId, this.projectId, this.projectName, this.mockId, this.mockName);
          this.isLoading = false;
        } catch (e) {
          this.isLoading = false;
        }
      },
      // Display the selected mock
      getMockData(mock, index) {
        this.isBusy = true;
        this.activeIndex = index;
        this.commentActiveIndex = 0;
        this.show_comment = false;
        this.mockId = mock.id;
        this.mockName = mock.name;
        if (mock.notes_json) {
          this.notes_json = JSON.parse(mock.notes_json);
        } else this.notes_json = [];
        this.isBusy = false;
      },
      // Save the added / edited comment
      async saveComment() {
        let notes_json = null;

        // Get the current date and time with Timezone
        let date = moment().format("llll");

        // Check if editing the existing comment
        if (this.commentIndex) {
          this.notes_json[this.commentIndex - 1].username = this.username;
          this.notes_json[this.commentIndex - 1].comment = this.edit_comment;
          this.notes_json[this.commentIndex - 1].commented_on = date.toString();
        } else {
          notes_json = {
            username: this.username,
            comment: this.comment,
            top: this.top,
            left: this.left,
            commented_on: date.toString(),
          };
          this.notes_json.push(notes_json);
        }
        notes_json = JSON.stringify(this.notes_json);
        this.hideCommentBox();
        await MockFrame.editMocks(this.domainId, this.projectId, this.projectName, this.mockId, this.mockName, notes_json);
        this.mocks = await MockFrame.getMocks(this.domainId, this.projectId, this.$route.query.p, this.mockId, this.$route.query.m);
      },
      // Get the selected comment
      getComment(comment, index) {
        this.commentActiveIndex = index;
        this.commentIndex = index + 1;
        this.label = "Edit comment";
        this.btn_comment = "Edit";
        this.edit_comment = comment.comment;
        this.username = comment.username;
        this.top = comment.top;
        this.left = comment.left;
      },
      // Delete comment
      async deleteComment(index) {
        this.notes_json.splice(index, 1);
        await MockFrame.editMocks(this.domainId, this.projectId, this.projectName, this.mockId, this.mockName, JSON.stringify(this.notes_json));
        this.mocks = await MockFrame.getMocks(this.domainId, this.projectId, this.$route.query.p, this.mockId, this.$route.query.m);
        this.hideCommentBox();
      },
      // Set the default values for comment box
      setDefaultValues() {
        this.label = "Add Comment";
        this.comment = "";
        this.edit_comment = "";
        this.username = "";
        this.btn_comment = "Add";
        this.commentIndex = 0;
      },
      // Display the comment box with default values (i.e. to Add a comment)
      showCommentBox() {
        this.show_comment = true;
        this.setDefaultValues();
      },
      // Hide the comment box and assign the default values to comment box
      hideCommentBox() {
        this.show_comment = false;
        this.setDefaultValues();
      },
      // Display the comment box for editing the selected comment
      showEditCommentBox(index) {
        if (index) {
          this.commentIndex = index;
          this.commentActiveIndex = index - 1;
        }
        this.label = "Edit Comment";
        this.btn_comment = "Edit";
        this.edit_comment = this.notes_json[this.commentIndex - 1].comment;
        this.username = this.notes_json[this.commentIndex - 1].username;
        this.top = this.notes_json[this.commentIndex - 1].top;
        this.left = this.notes_json[this.commentIndex - 1].left;
      },
    },
  };
</script>
<style>
  .active {
    background-color: #9eadbd40 !important;
  }
  .tooltip.b-tooltip {
    display: block !important;
    opacity: 0.9;
    outline: 0;
  }
  .tooltip.b-tooltip {
    pointer-events: none;
  }
</style>
